import React, { Component } from 'react';
import { Typography } from '@material-ui/core';
import PropTypes from 'prop-types';
import CountdownAnimation from '../../common/CountdownAnimation';

class GolfStarting extends Component {
  constructor(props) {
    super(props);
    this.startCounter();
    this.nextPhase = this.nextPhase.bind(this);
  }

  startCounter() {
    const that = this;
    let counter = 5;
    const i = setInterval(() => {
      counter -= 1;
      if (counter === 0) {
        that.nextPhase();
        clearInterval(i);
      }
    }, 1000);
    return 5;
  }

  nextPhase() {
    const { game, gameFunc } = this.props;

    game.phase = 'gameplay';
    gameFunc.update(game);
  }

  // skapa bara en klassic mode med en "boll" per spelare

  // i golf.js s sno kod fr att kunna skapa en golfbana med fysik osv.
  // skapa firebase lyssnare till varje boll? likt snake kanske. men granska den lsningen dden inte var s jttebra
  // skapa en enkel golfcontroller som kan gra en swing, f ett powervrde och skicka vrdet till firebaase och in i snake.js som slr till bollen utifrn vrdet
  // brja med en standard klubba/vinkel, i golfController ska man sen kunna ndra klubba innan varje slag.
  // putter, wedge, iron 8, iron 6, driver r standard?
  // vriga klubbtyper kan man kpa fr ingame currency som man vinner efter varje hl?
  // eller som kan kan kpa till sin "profil" krver att jag brjar med inloggning till stagehoot. krvs ocks fr att f till handikapp.
  // kanske bara fr golf man behver logga in? eller har valet att kunna logga in med frdelen att spara progress etc?
  render() {
    return (
      <div className="phase-container">
        <Typography variant="h2">Starting game</Typography>
        <CountdownAnimation speed="slow" />
      </div>
    );
  }
}
GolfStarting.propTypes = {
  gameFunc: PropTypes.object.isRequired,
  game: PropTypes.object.isRequired,
};
export default GolfStarting;
