function drawBall(
  x,
  y,
  stroke,
  playerColor,
  playerIndex,
  playerState,
  showNameTag,
  playerName,
  canvasHeight
) {
  // ball border width
  ctx.lineWidth = 1;

  // if (playerState && playerState === PlayerState.leftRound) {
  //   // make fill translucent
  //   ctx.fillStyle = tinycolor(fill).setAlpha(0.5).toRgbString();
  //   ctx.strokeStyle = tinycolor(stroke).setAlpha(0.5).toRgbString();
  // } else {
  //   ctx.fillStyle = fill;
  //   ctx.strokeStyle = stroke;
  // }
  ctx.fillStyle = playerColor;
  ctx.strokeStyle = stroke;
  ctx.beginPath();
  ctx.arc(x, y, BALL_RADIUS, 0, 2 * Math.PI);
  ctx.fill();
  ctx.closePath();

  ctx.beginPath();
  ctx.arc(x, y, BALL_RADIUS - ctx.lineWidth / 2, 0, 2 * Math.PI);
  ctx.stroke();
  ctx.closePath();

  if (
    showNameTag &&
    playerState &&
    (playerState === "STILL" || playerState === "MOVING")
  ) {
    let ballIndexMod = playerIndex % 10;
    let poleMin = Math.max(100, canvasHeight * 0.2);
    let poleMax = Math.max(150, canvasHeight * 0.4);
    let indexFactor = (poleMax - poleMin) / 10;
    let poleHeight = poleMin + ballIndexMod * indexFactor;

    ctx.lineWidth = 2;
    ctx.strokeStyle = playerColor;
    ctx.beginPath();
    ctx.moveTo(x, y);
    ctx.lineTo(x, y - poleHeight);
    ctx.stroke();
    ctx.closePath();

    ctx.font = "20px roboto";
    ctx.fillStyle = "#000000";
    ctx.textAlign = "center";
    ctx.fillText(playerName, x, y - poleHeight - 5);
  }
}

