import React, { Component } from 'react';
import { Typography } from '@material-ui/core';
import PropTypes from 'prop-types';
import CountdownAnimation from '../../../common/CountdownAnimation';

class SpotifyStarting extends Component {
  constructor(props) {
    super(props);
    this.startCounter();
    this.nextPhase = this.nextPhase.bind(this);
  }

  startCounter() {
    const that = this;
    let counter = 5;
    const i = setInterval(() => {
      counter -= 1;
      if (counter === 0) {
        that.nextPhase();
        clearInterval(i);
      }
    }, 1000);
    return 5;
  }

  nextPhase() {
    const { game, gameFunc } = this.props;
    // Generera frgor och svar. viktigt att gra detta i ett svep. vill inte behva anvnda spotify api under spelets gng fr att riskera att token har expirat
    // ALT 1
    // alla frgor och svar genereras hr och sparas till firebase
    // d kan jag lsa upp frgorna en i taget likt speltypen quiz och g till resultQuestion componenten
    // ALT 2
    // Alla frgor genereras i spotify.js utan att sparas till firebase
    // d mste resultquiz componenten vara en child-component till spotify.js.
    // spotify.js visas bde i phase gameplay och level_complete d och om det r level_complete s renderars childcomponent resultquiz

    game.phase = 'gameplay';
    gameFunc.update(game);
  }

  render() {
    return (
      <div className="phase-container">
        <Typography variant="h2">Starting game</Typography>
        <CountdownAnimation speed="slow" />
      </div>
    );
  }
}
SpotifyStarting.propTypes = {
  gameFunc: PropTypes.object.isRequired,
  game: PropTypes.object.isRequired,
};
export default SpotifyStarting;
